.Intro::after {
  content: "";
  left: -1440px;
  background: url('../images/chart.svg');
  background-repeat: repeat-x;
  height: 406px;
  width: calc(1440px * 5);
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 0;
  -webkit-animation-name: infiniteStats;
          animation-name: infiniteStats;
  -webkit-animation-duration: 120s;
          animation-duration: 120s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: none;
          animation-fill-mode: none;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes infiniteStats {
  0% {
    -webkit-transform: translate3d(-1440px, 0, 0);
            transform: translate3d(-1440px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes infiniteStats {
  0% {
    -webkit-transform: translate3d(-1440px, 0, 0);
            transform: translate3d(-1440px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}



